.ins-c-skeleton {
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: skeletonGradient;
  animation-timing-function: linear;
  background: var(--pf-global--BackgroundColor--disabled);
  background: linear-gradient(to right, #f5f5f5 10%, #e8e8e8 18%, #f5f5f5 33%);
  background-size: 1000px 50px;
  background-size: 62.5rem 3.125rem;
  border-radius: 5px;
  border-radius: 0.3125rem; }
  .ins-c-skeleton__xs {
    width: 16%; }
  .ins-c-skeleton__sm {
    width: 33%; }
  .ins-c-skeleton__md {
    width: 66%; }
  .ins-c-skeleton__lg {
    width: 100%; }

@keyframes skeletonGradient {
  0% {
    background-position: -500px 0px;
    background-position: -31.25rem 0rem; }
  100% {
    background-position: 500px 0px;
    background-position: 31.25rem 0rem; } }

